@ico-success: 'data:image/png;base64,';
@ico-error: 'data:image/png;base64,';
@keyframes zoomIn {
  from {
    opacity: 0;
    transform: scale3d(.3, .3, .3);
  }
  50% {
    opacity: 1;
  }
}
@keyframes bounceInDown {
  from, 60%, 75%, 90%, to {
    animation-timing-function: cubic-bezier(.215, .610, .355, 1.000);
  }
  0% {
    opacity: 0;
    transform: translate3d(0, -3000px, 0);
  }
  60% {
    opacity: 1;
    transform: translate3d(0, 25px, 0);
  }
  75% {
    transform: translate3d(0, -10px, 0);
  }
  90% {
    transform: translate3d(0, 5px, 0);
  }
  to {
    transform: none;
  }
}
.m-confirm {
  position: fixed;
  z-index: 999;
  width: 85%;
  top: 30%;
  left: 50%;
  background-color: #FAFAFC;
  text-align: center;
  border-radius: 3px; /* no */
  margin-left: -42.5%;
  animation: bounceInDown .3s ease forwards;
  &.alert {
    animation: zoomIn .3s ease forwards;
    .confirm-hd, .confirm-bd {
      text-align: center
    }
  }
  .confirm-hd {
    text-align: left;
    padding: 15px 20px 10px; /* no */
    .confirm-title {
      font-weight: 400;
      font-size: 17px; /* no */
    }
  }
  .confirm-bd {
    text-align: left;
    padding: 0 20px; /* no */
    font-size: 15px; /* no */
    color: #888;
    line-height: 20px; /* no */
  }
  .confirm-ft {
    position: relative;
    line-height: 42px; /* no */
    margin-top: 15px; /* no */
    font-size: 17px; /* no */
    border-top: 1px solid #E7E7E9; /* no */
    overflow: hidden;
    display: flex;
    a {
      position: relative;
      width: 1%;
      display: block;
      flex: 1;
      &.confirm-btn.default {
        color: #353535;
      }
      &.confirm-btn.primary {
        color: #0BB20C;
        &:after {
          content: '';
          position: absolute;
          left: 0;
          top: 0;
          width: 2px; /* no */
          height: 42px; /* no */
          background-color: #E7E7E9;
          transform-origin: 0 0;
          transform: scaleX(.5);
        }
      }
    }
  }
}
@media screen and (min-width: 768px) {
  .m-confirm {
    width: 40%;
    margin-left: -20%;
  }
}
.m-tipmes {
  position: fixed;
  width: 140px; /* no */
  top: 25%;
  left: 50%;
  margin-left: -70px; /* no */
  background: rgba(40, 40, 40, .75);
  text-align: center;
  border-radius: 5px; /* no */
  color: #FFF;
  z-index: 999;
  animation: zoomIn .06s ease-in forwards;
  .tipmes-content {
    font-size: 14px; /* no */
    padding: 0 10px 15px; /* no */
    line-height: 22px; /* no */
  }
  .tipmes-success-ico, .tipmes-error-ico {
    margin: 22px 0 0; /* no */
    display: block;
    margin-bottom: 10px; /* no */
    &:after {
      display: inline-block;
      content: '';
    }
  }
  .tipmes-success-ico:after {
    width: 50px; /* no */
    height: 41px; /* no */
    background: url(@ico-success) no-repeat;
    background-size: 50px 41px; /* no */
  }
  .tipmes-error-ico:after {
    width: 40px; /* no */
    height: 40px; /* no */
    background: url(@ico-error) no-repeat;
    background-size: 40px 40px; /* no */
  }
}
.m-loading {
  position: fixed;
  z-index: 9999;
  width: 120px; /* no */
  top: 27%;
  left: 50%;
  margin-left: -60px; /* no */
  border-radius: 5px; /* no */
  color: #FFF;
  background: rgba(40, 40, 40, .75);
  text-align: center;
  animation: zoomIn .1s ease forwards;
  .ld-loading {
    position: absolute;
    width: 0;
    z-index: 99999;
    left: 50%;
    top: 43px; /* no */
  }
  .ld-toast-content {
    margin: 75px 5px 15px; /* no */
    font-size: 14px; /* no */
    line-height: 20px; /* no */
  }
}
.m-loading-leaf {
  position: absolute;
  top: -1px; /* no */
  opacity: .25;
  &:before {
    content: '';
    position: absolute;
    width: 10px; /* no */
    height: 3px; /* no */
    background-color: rgb(209, 209, 213);
    box-shadow: rgba(0, 0, 0, .098) 0 0 1px; /* no */
    border-radius: 1px; /* no */
    transform-origin: left 50% 0px; /* no */
  }
  &-0 {
    animation: opacity-60-25-0-12 1.25s linear infinite;
    &:before {
      transform: rotate(0deg) translate(10px, 0px); /* no */
    }
  }
  &-1 {
    animation: opacity-60-25-1-12 1.25s linear infinite;
    &:before {
      transform: rotate(30deg) translate(10px, 0px); /* no */
    }
  }
  &-2 {
    animation: opacity-60-25-2-12 1.25s linear infinite;
    &:before {
      transform: rotate(60deg) translate(10px, 0px); /* no */
    }
  }
  &-3 {
    animation: opacity-60-25-3-12 1.25s linear infinite;
    &:before {
      transform: rotate(90deg) translate(10px, 0px); /* no */
    }
  }
  &-4 {
    animation: opacity-60-25-4-12 1.25s linear infinite;
    &:before {
      transform: rotate(120deg) translate(10px, 0px); /* no */
    }
  }
  &-5 {
    animation: opacity-60-25-5-12 1.25s linear infinite;
    &:before {
      transform: rotate(150deg) translate(10px, 0px); /* no */
    }
  }
  &-6 {
    animation: opacity-60-25-6-12 1.25s linear infinite;
    &:before {
      transform: rotate(180deg) translate(10px, 0px); /* no */
    }
  }
  &-7 {
    animation: opacity-60-25-7-12 1.25s linear infinite;
    &:before {
      transform: rotate(210deg) translate(10px, 0px); /* no */
    }
  }
  &-8 {
    animation: opacity-60-25-8-12 1.25s linear infinite;
    &:before {
      transform: rotate(240deg) translate(10px, 0px); /* no */
    }
  }
  &-9 {
    animation: opacity-60-25-9-12 1.25s linear infinite;
    &:before {
      transform: rotate(270deg) translate(10px, 0px); /* no */
    }
  }
  &-10 {
    animation: opacity-60-25-10-12 1.25s linear infinite;
    &:before {
      transform: rotate(300deg) translate(10px, 0px); /* no */
    }
  }
  &-11 {
    animation: opacity-60-25-11-12 1.25s linear infinite;
    &:before {
      transform: rotate(330deg) translate(10px, 0px); /* no */
    }
  }
}
